@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;1,100;1,300;1,400&display=swap);
.grid-container a,footer a{text-decoration:none;outline:none;text-decoration:none !important}html{box-sizing:border-box;font-size:62.5%;margin:0px}body,#root{margin:0;font-family:"Roboto",sans-serif;height:100vh;font-size:1.6rem}.grid-container{display:grid;grid-template-areas:'header' 'main' 'footer';grid-template-rows:5rem 1fr 5rem;grid-template-columns:1fr;height:100%}header{grid-area:header;color:#fff;background-color:#000;display:flex;align-items:center;padding:.5rem}header a{color:white}main{grid-area:main}.content{display:flex;flex-wrap:wrap;overflow:auto}.main{flex:4 1 39rem;height:100vh}.main a{color:#000}.sidebar{flex:1 1 20rem;align-self:flex-start;position:-webkit-sticky;position:sticky;top:0px;background-color:rgba(246,246,246,0.8);border-bottom-left-radius:30px}footer{grid-area:footer;display:flex;justify-content:center;align-items:center;background-color:#000;color:white;font-size:1.5rem;font-weight:300;letter-spacing:.2rem;flex-wrap:wrap}footer a{margin-left:.5rem;color:#ffc7c7}footer a:hover{transform:scale(1.1)}@media only screen and (max-width: 767px){.sidebar{display:none}}

.products{display:flex;justify-content:center;align-items:center}.products{padding-bottom:5rem;margin:0;list-style-type:none;flex-wrap:wrap}.products li{flex:0 1 29rem;height:50rem;padding:2rem}.product{display:flex;flex-direction:column;justify-content:space-between;height:100%}.product img{min-width:35rem;max-height:40rem}.title{text-transform:uppercase;font-family:'Montserrat', sans-serif;outline:none;text-decoration:none;outline:none;font-size:1.5rem}.product-price{display:flex;justify-content:space-between;align-items:center}button,.price{text-align:center;flex:1 1}div{font-size:2rem}.button{padding:1rem;border:0.1rem #c0c0c0 solid;background-color:#fff;outline:none}.full{background-color:#000;color:white;border:none;padding:2rem}.outline{outline:2px solid #000;color:#000;font-weight:700}.primary{font-weight:700}.primary:hover,.outline:hover{border:0.1rem #000 solid;background-color:#000;color:white}.product-info{display:flex;flex-wrap:wrap}.product-info img{max-width:100vw;max-height:70vh}.product-info .info-right{margin:3rem 5rem;flex:1 1}.product-info .title{font-weight:600;font-size:2.5rem}.product-info .size{border-radius:50%;padding:1rem;min-height:5rem;min-width:5rem;margin:.5rem}.product-info .product-info-description{border-top:1px solid #c0c0c0;margin:3rem auto;width:100%}

.filter{display:flex;justify-content:space-between;align-items:center}.filter{padding:1rem;margin:1rem 2rem;border-bottom:0.1rem #f6f6f6 solid;flex-wrap:wrap}.filter div{flex:1 1}.filter select{outline:none;padding:.5rem;margin-left:.5rem;border-radius:15px}

.cart{padding:1rem;margin:1rem;display:flex;flex-wrap:wrap}.cart .cart-svg{width:8rem;margin:3rem;text-align:center}.cart-items{padding:0;list-style-type:none;width:100%}.cart-items img{width:5rem}.cart-items li{display:flex;font-weight:300}.cart-items li div{padding:.5rem}.cart-items li .price{font-size:1.5rem;font-weight:300;letter-spacing:.1rem}.total{display:flex;justify-content:space-between;align-items:center;width:100%}.total div{flex:1 1;font-size:2rem}

.form-container{margin-top:4rem;border-top:1px solid #c0c0c0;padding:0;list-style-type:none;width:100%}.form-container li{display:flex;flex-direction:column;padding:1rem}.form-container input{padding:1rem;border:.1rem #c0c0c0 solid;outline:none}.form-container input:hover{outline:1px solid black}

